.current-plan-card {
  width: 100%;
  background: rgba(255, 255, 255, 0.25);
  // box-shadow: 0 8px 32px 0 rgba(195, 197, 219, 0.37);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  border-radius: 10px;
  border: 1px solid rgba(184, 184, 184, 0.18);

  display: grid;
  grid-template-columns: 5fr 1.8fr;
  grid-column-gap: 25px;
}

.current-plan-name {
  color: #538bf7;
  font-weight: 700;
  letter-spacing: 1px;
  font-size: larger;
}

.plan-limit-left-message {
  color: #464646;
  font-family: 'Inter';
  font-weight: 500;
  font-size: 15px;
}

.plan-card-upgrade-message {
  font-size: 13px;
}

.current-plan-will-renew-at {
  color: #646464;
}

.plan-renewal-date {
  font-family: 'Inter';
  font-weight: 500;
  font-size: 15px;
}

.pricing-cards-section {
  width: 100%;
  min-height: 600px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 25px;
}

.pricing-plan-card {
  height: 100%;

  background: rgba(228, 228, 228, 0.25);
  box-shadow: 0 8px 32px 0 rgba(195, 197, 219, 0.37);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
  border-radius: 10px;
  border: 1px solid rgba(184, 184, 184, 0.18);
}

.pricing-plan-name {
  font-size: 22px;
  font-weight: 600;
  font-family: 'Inter';
  color: #3a3a3a;
}

.original-plan-price-value {
    text-decoration: line-through;
    font-size: 20px;
  font-weight: 500;
  font-family: 'Inter';
  color: #7e7e7e;

}

.original-plan--reduced-price-value {
    font-size: 20px;
  font-weight: 500;
  font-family: 'Inter';
  color: #575757;
}

.plan-tax-text {
    font-size: 12px;
    font-weight: 500;
    font-family: 'Inter';
    color: #575757;
}

.plan-feature-text {
    font-size: 13px;
    letter-spacing: 0.5px;
    font-weight: 500;
    font-family: 'Inter';
    color: #4d4d4d;
}

.stripe-connect {
  background: #635bff;
  display: inline-block;
  height: 38px;
  text-decoration: none;
  width: 180px;

  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;

  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;

  -webkit-font-smoothing: antialiased;
}

.stripe-connect span {
  color: #ffffff;
  display: block;
  font-family: sohne-var, "Helvetica Neue", Arial, sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 14px;
  padding: 11px 0px 0px 24px;
  position: relative;
  text-align: left;
}

.stripe-connect:hover {
  background: #7a73ff;
}

.stripe-connect.slate {
  background: #0a2540;
}

.stripe-connect.slate:hover {
  background: #425466;
}

.stripe-connect.white {
  background: #ffffff;
}

.stripe-connect.white span {
  color: #0a2540;
}

.stripe-connect.white:hover {
  background: #f6f9fc;
}

.stripe-connect span::after {
  background-repeat: no-repeat;
  background-size: 49.58px;
  content: "";
  height: 20px;
  left: 62%;
  position: absolute;
  top: 28.95%;
  width: 49.58px;
}

