.event-card-main {
  width: 100%;
  max-width: 500px;

  border-radius: 20px;
  padding: 0 !important;

  box-shadow: 0 4px 2px 0 rgba(233, 233, 233, 0.609),
    0 6px 20px 0 rgba(196, 195, 195, 0.746);
  cursor: pointer;
  /* filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.2)); */
  transition: all 0.2s ease-in-out;
  
  img {
    transition: all 0.2s ease-in-out;
  }
}

.event-card-img-container {
  position: relative;
  width: 100%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.event-card-like-icon {
  position: absolute;
  top: 8%;
  right: 5%;
  background-color: #ffffff;
  border-radius: 50%;
}

.poster-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

/* .event-card-text-info {
    
} */

.event-card-date-main {
  font-family: 'Inter';
  font-style: normal;
  font-weight: bold;
  font-size: 0.9rem;
  line-height: 20px;
  /* identical to box height */

  text-align: left;

  color: #757575;
}

.event-card-name-main {
  font-family: 'Inter';
  font-style: normal;
  font-weight: bold;
  font-size: 1.25rem;
  line-height: 24px;
  /* or 120% */

  color: #212121;
}

.event-card-price-main {
  font-family: 'Inter';
  font-style: normal;
  font-weight: bold;
  font-size: 0.9rem;
  line-height: 20px;
  /* identical to box height */

  text-align: left;

  color: rgba(117, 117, 117, 0.952);
}

.you-have-no-event-coming-card {
  background: #ffffff;
  box-shadow: 0px 0px 30px rgba(176, 195, 211, 0.16);
  border-radius: 20px;

  /* height: 300px; */
  width: 100%;
  min-width: 556px;
}

.you-have-no-event-coming-text {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 0.95rem;
  line-height: 18px;
  /* identical to box height, or 129% */

  color: #757575;
}

.btn-text-customised {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 0.95rem;
  line-height: 16px;
  /* identical to box height, or 114% */

  color: #ffffff;
}

.btn-color-customised {
  background-color: #538bf7;
}

.event-card-main:hover {
  box-shadow: 0 4px 2px 0 rgba(233, 233, 233, 0.609),
    0 6px 20px 0 rgba(196, 195, 195, 0.746);
  cursor: pointer;
  /* filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.2)); */
  transition: all 0.2s ease-in-out;
  img {
    object-fit: cover;
  }
}

.rating-indicator {
  background-color: #538bf7;
  border-radius: 5px;
  color: #ffffff;
  font-weight: 500;
  font-size: 0.8rem;
}

.favourite-icon:hover {
  fill: #c2351c;
}

.demo-event-card:hover {
  box-shadow: 0 4px 2px 0 rgba(128, 128, 128, 0.609),
    0 6px 20px 0 rgba(124, 124, 124, 0.746);
  cursor: pointer;
  /* filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.2)); */
  transition: all 0.2s ease-in-out;
}
