/* Loader Styling */

.blob-7 {
  width: 100px;
  height: 100px;
  display: grid;
  background: #fff;
  -webkit-filter: blur(5px) contrast(10) hue-rotate(300deg);
          filter: blur(5px) contrast(10) hue-rotate(300deg);
  padding: 10px;
  mix-blend-mode: darken;
}
.blob-7:before,
.blob-7:after {
  content: "";
  grid-area: 1/1;
  width: 40px;
  height: 40px;
  background: #ffff00;
  -webkit-animation: bo7 2s infinite;
          animation: bo7 2s infinite;
}
.blob-7:after {
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}

.nav-dropdown-item-active-icon:hover {
  fill: #538bf7;
}

.nav-dropdown-item-active-text:hover {
  color: #538bf7;
}

@-webkit-keyframes bo7 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  25% {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
  }
  50% {
    -webkit-transform: translate(100%, 100%);
            transform: translate(100%, 100%);
  }
  75% {
    -webkit-transform: translate(0, 100%);
            transform: translate(0, 100%);
  }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes bo7 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  25% {
    -webkit-transform: translate(100%, 0);
            transform: translate(100%, 0);
  }
  50% {
    -webkit-transform: translate(100%, 100%);
            transform: translate(100%, 100%);
  }
  75% {
    -webkit-transform: translate(0, 100%);
            transform: translate(0, 100%);
  }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

