.rbc-current-time-indicator {
  position: absolute;
  z-index: 3;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #74ad31;
  pointer-events: none;
}

.blockbuilder-branding {
  visibility: hidden !important;
  display: none !important;
}

.brand {
  visibility: hidden !important;
  display: none !important;
}

.ripple {
  text-decoration: none;
  color: #fff;
  width: 12px;
  height: 12px;
  background-color: #00E7FF;
  margin: 0 auto;
  border-radius: 100px;
  -webkit-animation: ripple 0.7s linear infinite;
  animation: ripple 0.7s linear infinite;
}

@-webkit-keyframes ripple {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 231, 255, 0.3), 0 0 0 10px rgba(0, 231, 255, 0.3), 0 0 0 30px rgba(0, 231, 255, 0.3), 0 0 0 60px rgba(0, 231, 255, 0.3);
  }
  100% {
    box-shadow: 0 0 0 10px rgba(0, 231, 255, 0.3), 0 0 0 30px rgba(0, 231, 255, 0.3), 0 0 0 60px rgba(0, 231, 255, 0.3), 0 0 0 90px rgba(0, 231, 255, 0);
  }
}
@keyframes ripple {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 231, 255, 0.3), 0 0 0 10px rgba(0, 231, 255, 0.3), 0 0 0 30px rgba(0, 231, 255, 0.3), 0 0 0 60px rgba(0, 231, 255, 0.3);
  }
  100% {
    box-shadow: 0 0 0 10px rgba(0, 231, 255, 0.3), 0 0 0 30px rgba(0, 231, 255, 0.3), 0 0 0 60px rgba(0, 231, 255, 0.3), 0 0 0 90px rgba(0, 231, 255, 0);
  }
}

.App {
  text-align: center;
}

.App-spacer {
  width: 0;
  height: 0;
  margin-top: 100px;
}

.App-container {
  background: #282c34;
  padding: 20px 20px;
}

.carousel-container-video, .carousel-container-products, .carousel-container-links {
  width: 850px;
}

.carousel-container-forms, .carousel-container-offers{
  width: 260px;
}

.carousel-item-forms, .carousel-item-offers {
  width: 100%;
}

.carousel-item-video {
  width: 370px;
  padding-right: 10px;
  padding-left: 10px;
}

.carousel-item-products {
